/* COLORS */
$white: #FFFFFF;
$light-grey: #F6F4F4;
$beige-bright: #A89B94;
$beige-medium: #918178;
$beige-dark: #655953;
$blue-dark: #2F4858;
$green-dark: #2F474B;
$orange-brown: #A66A5E;
$brown: #A28967;
$black: #000000;
$red: #D40D0D;
$green: #049f44;

// COLORS WITH TRANSPARENCY
$transparent-white: transparentize($color: $white, $amount: 0.40);

* {
	font-family: "Heebo", Helvetica, sans-serif;
	font-weight: 300;
	line-height: 1.5;
}

.heading, h1, h2, h3, h4, h5 {
	font-family: 'Playfair Display', serif;
  font-weight: 400;
  color: $blue-dark;
}

.heading--h1 {
  font-size: 80px;
  font-weight: 600;	
	line-height: 1.2;
}

.heading--h2 {
  font-size: 240px;
  font-weight: 400;
	line-height: 1;
	opacity: 0.08;
	white-space: nowrap;
	text-transform: uppercase;
}

.heading--h3 {
  font-size: 48px;
  font-weight: 400;
	line-height: 1.2;
}

.heading--h4 {
  font-size: 20px;
  font-weight: 500;
	line-height: 1.2;
}

.paragraph, p {
	font-size: 18px;
	font-family: "Heebo", Helvetica, sans-serif;

	&--bold { font-weight: 600; }
	&--small { font-size: 14px; }
	&--big { font-size: 32px; font-family: "Playfair Display"; }

}

.link, a {
	font-family: "Heebo", Helvetica, sans-serif;
	font-size: 18px;
	line-height: 26px;
	line-height: 1.2;
	text-decoration: none;
	font-weight: 400;

	&--small {
		font-size: 14px;
	}

	&--serif {
		font-family: 'Playfair Display', serif;
		font-weight: 400;
		font-size: 32px;
	}

}

.highlight {
	font-weight: inherit;
	font-family: inherit;
}

.text-input__input, 
.textarea__input {
	font-size: 18px;
	font-weight: 300;
}

.text-input__label, 
.textarea__label {
	font-size: 18px;
	font-weight: 300;
}


* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

.body {
	width: 100%;
	max-width: 100vw;
	overflow-x: hidden;

	&--dark { background-color: $green-dark; }
	&--light { background-color:$light-grey;  }

}



.circle-link {
	color: $white;
	display: flex;
	flex-direction: row;
	align-items: center;

	&::after { 
		content: "\ea03";
		margin-left: 16px;
		font-family: Icon;
		transform: rotate(270deg);
		width: 32px;
		height: 32px;
		display: flex;
		cursor: pointer;
		align-items: center;
		justify-content: center;
		vertical-align: center;
		text-align: center;
		color: $white !important;
		padding: 8px;
		border: 1px solid $white;
		border-radius: 50%;
	}
	
}
.cursor {
	pointer-events: none;

	&__main {
		position: fixed;
		top: -100px;
		left: -100px;
		width: 8px;
		height: 8px;
		background-color: transparent;
		transform: translate(-50%, -50%);
		border-radius: 50%;
		border: 1px solid $white;
		z-index: 20;
	}
	
	&__follower {
		position: fixed;
		top: -100px;
		left: -100px;
		width: 32px;
		height: 32px;
		transform: translate(-50%, -50%);
		background-color: $orange-brown;
		border-radius: 50%;
	}
		
}
.external-link {
	border-bottom: 1px solid $beige-medium;
	color: $beige-medium;
	font-weight: 300;
	cursor: pointer;
	font-size: 24px;

	&__icon {
		transform: rotate(230deg);
		position: relative;
		right: -4px;
		font-size: 24px !important;
		color: $beige-medium !important;
	}

}
.heading {
  color: $blue-dark;
	 
  &--black { color: $black }
  &--beige-dark { color: $beige-dark }
  &--blue-dark { color: $blue-dark }
	&--white { color: $white }

	.heading--h2 {
		
		&--beige { color: $beige-medium }
		&--white { color: $white }
		
	}

}
.hello-world {
	color: white;
	text-shadow: 0px 0px 2px $blue-dark;
}
.highlight {
	color: $black;

  &--black { color: $black; }
  &--beige-dark { color: $beige-dark; }
  &--blue-dark { color: $blue-dark; }
	&--white { color: $white; }

}
.icon {
  display: inline-block;
  font: normal normal normal 32px 'Icon';
  color: inherit;
  flex-shrink: 0;
	color: $beige-dark;
  text-transform: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon--small { font-size: 12px; }
.icon--medium { font-size: 16px; }
.icon--large { font-size: 24px; }
.icon--big { font-size: 32px; }

.icon--angle::before { content: "\ea02"; }
.icon--arrow::before { content: "\ea03"; }
.icon--linkedin::before { content: "\ea04"; }
.icon--close::before { content: "\ea05"; }
 .gravity-link {
	 padding: 16px;
	 color: $white;
	 cursor: pointer;
	 display: inline-block;

	 &--black { color: $black; }
	 &--beige-dark { color: $beige-dark; }
	 &--blue-dark { color: $blue-dark; }
	 &--white { color: $white; }

 }
.paragraph {

  &--black { color: $black; }
  &--beige-dark { color: $beige-dark; }
  &--beige-medium { color: $beige-medium; }
  &--blue-dark { color: $blue-dark; }
	&--white { color: $white; }

}
.quote {

	&::before { content: "\“"; }
	&::after { content: "\”"; }

}
.switcher {
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: center;
	width: 280px;
	height: 120px;

	&__label, .label {
		display: flex;
		flex-direction: row;
		justify-content: center;
		align-items: center;
		width: 100%;
		height: 100%;
    background-color: rgba(255, 255, 255, 0.7);
    backdrop-filter: blur(10px) brightness(1.2);

		&__text {
			color: $white;
		}

		&__icon {
			transform: rotate(270deg);
			color: $white;
			position: relative;
			left: 0;
			transition: left 0.1s ease-in-out;
		}

	}

	&:hover {

		.label__icon {
			left: 8px;
		}

	}

}
.text-input {
	width: 100%;
	position: relative;
	
	&__input {
		width: 100%;
		height: 48px;
		color: $white;
		padding: 8px 0;
		outline: none;
		border: none;
		border-bottom: 1px solid $transparent-white;
		padding: 0;
		background-color: transparent;
		margin-top: 40px;

		&:focus + .text-input__label,
		&:not(:placeholder-shown) + .text-input__label,
		&--active + .text-input__label {
			transform: translate(-16px, -32px);
			font-size: 12px;
		}

		&--error {
			border-bottom: 1px solid $red;
			
			& ~ .text-input__error-message {
				display: block;
			}
			
		}
	}
	
	&__label {
		position: absolute;
		color: $transparent-white;
		bottom: calc(50% - (24px / 2));
		top: 48px;
		left: 16px;
		transition: all 0.2s ease;
		pointer-events: none;
	}

	&__error-message {
		color: $red;
		padding-top: 8px;
		display: none;
	}

}
.textarea {
	width: 100%;
	position: relative;
	
	&__input {
		width: 100%;
		display: block;
		height: 120px;
		color: $white;
		outline: none;
		border: none;
		border-bottom: 1px solid $transparent-white;
		padding: 0;
		background-color: transparent;
		margin-top: 40px;
		resize: none;
		
		&:focus + .textarea__label,
		&:not(:placeholder-shown) + .textarea__label,
		&--active + .textarea__label {
			transform: translate(-16px, -32px);
			font-size: 12px;
		}

		&--error {
			border-bottom: 1px solid $red;
			
			& ~ .text-input__error-message {
				display: block;
			}
			
		}

	}
	
	&__label {
		position: absolute;
		color: $transparent-white;
		top: 48px;
		left: 16px;
		transition: all 0.2s ease;
		pointer-events: none;
	}

	&__error-message {
		color: $red;
		padding-top: 8px;
		display: none;
	}

}
.contact-form {
	display: flex;
	flex-direction: column;
	gap: 16px;

	&__disclaimer, .disclaimer {

		&__text {
			color: $white;
		}

		&__link {
			color: white;
			text-decoration: underline;
			font-weight: inherit;
		}

	}

	&__submit {
		align-self: flex-end;
	}

	&__status-message, .status-message {

		&--success, 
		&--error {
			display: none;
			color: $white;
			text-align: center;
			position: relative;	

			&::after {
				content: "";
				position: absolute;
				width: 4px;
				height: 4px;
				top: 12px;
				display: inline-block;
				margin-left: 8px;
				background-color: $red;
				border-radius: 50%;
			}

		}

		&--success::after { background-color: $green; }
		&--error::after { background-color: $red; }

		&--active { display: block }

	}

}